.about {
    display: flex;
}

.about__wrapper {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    max-width: 1140px;
}

.about__wrapper div:first-of-type {
    height: 562px;
}

.about__wrapper img {
    width: 562px;
}

.about__desc {
    padding: 70px 60px 50px 80px;
    box-sizing: border-box;
    z-index: -1;
    margin-left: -40px;
    background-color: #FFD800;
}

.about__title {
    font-size: 25px;
    line-height: 34px;
    font-weight: 700;
    color: #161922;
}

.about__text {
    margin-bottom: 50px;
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
}

.about button {
    padding: 11px 20px;
    font-size: 15px;
    font-weight: 700;
    font-family: 'Open Sans', sans-serif;
    color: white;
    background-color: #10293d;
    border: none;
}

.about button:hover {
    color: #FFD800;
}