.service {
    margin-top: 100px;
    display: flex;
}

.service__wrapper {
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
}

.service__title {
    margin-bottom: 70px;
    font-size: 30px;
    line-height: 40px;
    font-weight: 700;
    width: 40%;
    color: #161922;
}

.features__wrapper {
    display: flex;
    justify-content: space-between;
}

.features__item {
    width: 33%;
}

.features__item:not(:last-of-type) {
    margin-right: 60px;
}

.features__item img {
    width: 204px;
}

.features__item h4 {
    margin-bottom: 0;
    color: #161922;
    font-size: 25px;
    line-height: 34px;
    font-weight: 700;
}

.features__item p {
    color: #10293d;
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
}

.features__item span {
    color: #161922;
    font-size: 15px;
    line-height: 25px;
    font-weight: 700;
}