.hero {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 880px;
}

.hero>img {
    position: absolute;
    top: 0;
    right: -130px;
    z-index: -1;
}

.hero__wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 1140px;
}

.hero__title {
    width: 65%;
    font-weight: 700;
    font-size: 70px;
    line-height: 80px;
    color: rgb(16, 41, 61);
}

.hero__subtitle {
    margin-bottom: 60px;
    width: 65%;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    color: rgb(16, 41, 61);
}

.hero__wrapper>button {
    background-color: rgb(255, 216, 0);
    font-family: 'Open Sans', sans-serif;
    line-height: 40px;
    font-weight: 700;
    font-size: 16px;
    padding: 4px 40px;
    color: rgb(16, 41, 61);
    border: none;
}

.hero__wrapper>button:hover {
    background-color: #10293d;
    color: #ffffff;
}

.hero__line {
    margin-top: 60px;
    margin-bottom: 30px;
    height: 2px;
    width: 400px;
    background-color: rgb(218, 218, 218);
}

.social {
    width: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.social p {
    font-family: 'Open Sans', sans-serif;
    line-height: 23px;
    font-weight: 400;
    font-size: 15px;
    color: rgba(16, 41, 61, 0.79);
}

.social ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.social ul li {
    background-color: rgb(242, 242, 242);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social ul li:not(:last-of-type) {
    margin-right: 10px;
}

.social ul a {
    color: rgba(16, 41, 61, 0.79);
}