.contactUs {
    margin-top: 100px;
    display: flex;
}

.contactUs__wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 1140px;
    display: flex;
}

.contactUs__wrapper img {
    width: 40%;
    height: auto;
    max-height: 479px;
    margin-right: 40px;
}

.contactUs__desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.contactUs__title {
    font-size: 30px;
    line-height: 40px;
    font-weight: 700;
    color: #161922;
}

.contactUs__desc div {
    margin: 35px 0;
}

.contactUs__desc span {
    font-size: 19px;
    line-height: 27px;
    font-weight: 700;
    color: #161922;
}

.contactUs__desc span:not(:last-of-type) {
    margin-right: 30px;
}

.contactUs__desc p {
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
}
